.button {
    /* flex-basis: 10rem; */
    width: 100%;
    height: 3rem;
    border: none;
    border-radius: 0.3rem;
    background-color: blueviolet;
    color: white;
    cursor: pointer;
}
.button-link {
    display: flex;
    width: 10rem;
    min-width: 5rem;
    text-decoration: none;
}
